import React from 'react';

const Section3 = () => (
      <div className="section text-block animate-section">
          <div className="text-top">
              <div className="text-top-wrapper">
                  <p><span className="tick-top"></span></p>
                  <p>360</p>
                  <p>30</p>
              </div>
          </div>
          <div className="text-bottom">
              <div className="text-bottom-contain">
                  <div className="text-bottom-left"></div>
                  <div className="text-bottom-wrapper">
                      <p><span className="tick-bottom"></span></p>
                      <p></p>
                      <p>Are you Ready</p>
                  </div>
              </div>
          </div>
      </div>
);

export default Section3;


